<template>
  <div class="p2">

    <p style="font-size:22px;height:30px; font-weight:bold;">增值税减免税申报明细表（季度）</p>
    <p style="text-align: left;">税款所属时间：自{{com.month}}-01日至{{com.month}}-30日</p>
    <p style="width:20%; float:left;height:25px; text-align:left;">纳税人名称：</p>
    <p style="width:50%; float:left;height:25px; text-align:left;">{{com.firm_name}}</p>
    <p style="width:30%; float:left;height:25px; text-align:right;">金额单位：元至角分</p>
    <form class="biaoge">
      <table cellpadding="0" cellspacing="0" border="1" width="100%">
        <tr>
          <td colspan="7">一、减税项目</td>
        </tr>
        <tr>
          <td rowspan="2" width="240">减税性质代码及名称</td>
          <td rowspan="2">栏次</td>
          <td>期初余额</td>
          <td>本期发生额</td>
          <td>本期应抵减税额</td>
          <td>本期实际抵减税额</td>
          <td>期末余额</td>
        </tr>
        <tr>
          <td>1</td>
          <td>2</td>
          <td>3=1+2</td>
          <td>4≤3</td>
          <td>5=3-4</td>
        </tr>
        <tr>
          <td>合计</td>
          <td>1</td>
          <td style="background-color:#ccccff;"><input name="1_1" readonly value="0.00"/></td>
          <td style="background-color:#ccccff;"><input name="1_2" readonly value="0.00"/></td>
          <td style="background-color:#ccccff;"><input name="1_3" readonly value="0.00"/></td>
          <td style="background-color:#ccccff;"><input name="1_4" readonly value="0.00"/></td>
          <td style="background-color:#ccccff;"><input name="1_5" readonly value="0.00"/></td>
        </tr>
        <tr>
          <td style="background-color:#d6ebff"><input name="typA" type="text" bt="text" style="text-align: center;"/>
          </td>
          <td>2</td>
          <td style="background-color:#d6ebff;"><input name="2_1" value="0.00"/></td>
          <td style="background-color:#d6ebff;"><input name="2_2" value="0.00"/></td>
          <td style="background-color:#ccccff;"><input name="2_3" readonly value="0.00"/></td>
          <td style="background-color:#d6ebff;"><input name="2_4" value="0.00"/></td>
          <td style="background-color:#ccccff;"><input name="2_5" readonly value="0.00"/></td>
        </tr>
        <tr>
          <td colspan="7">二、免税项目</td>
        </tr>
        <tr>
          <td rowspan="2">免税性质代码及名称</td>
          <td rowspan="2">栏次</td>
          <td>免征增值税项目销售额</td>
          <td>免税销售额扣除项目本期实际扣除金额</td>
          <td>扣除后免税销售额</td>
          <td>免税销售额对应的进项税额</td>
          <td>免税额</td>
        </tr>
        <tr>
          <td>1</td>
          <td>2</td>
          <td>3=1-2</td>
          <td>4</td>
          <td>5</td>
        </tr>
        <tr>
          <td>合计</td>
          <td>3</td>
          <td style="background-color:#ccccff;"><input name="3_1" readonly value="0.00"/></td>
          <td style="background-color:#ccccff;"><input name="3_2" readonly value="0.00"/></td>
          <td style="background-color:#ccccff;"><input name="3_3" readonly value="0.00"/></td>
          <td style="background-color:#ccccff;"><input name="3_4" readonly value="0.00"/></td>
          <td style="background-color:#ccccff;"><input name="3_5" readonly value="0.00"/></td>
        </tr>
        <tr>
          <td>出口免税</td>
          <td>4</td>
          <td style="background-color:#d6ebff;"><input name="4_1" value="0.00"/></td>
          <td style="background-color:#FFF;">------</td>
          <td style="background-color:#FFF;">------</td>
          <td style="background-color:#FFF;">------</td>
          <td style="background-color:#FFF;">------</td>
        </tr>
        <tr>
          <td>其中：跨境服务</td>
          <td>5</td>
          <td style="background-color:#d6ebff;"><input name="5_1" value="0.00"/></td>
          <td style="background-color:#FFF;">------</td>
          <td style="background-color:#FFF;">------</td>
          <td style="background-color:#FFF;">------</td>
          <td style="background-color:#FFF;">------</td>
        </tr>
        <tr>
          <td style="background-color:#d6ebff"><input name="typB" bt="text" type="text" style="text-align: center;"/>
          </td>
          <td>6</td>
          <td style="background-color:#d6ebff;"><input name="6_1" value="0.00"/></td>
          <td style="background-color:#d6ebff;"><input name="6_2" value="0.00"/></td>
          <td style="background-color:#ccccff;"><input name="6_3" readonly value="0.00"/></td>
          <td style="background-color:#d6ebff;"><input name="6_4" value="0.00"/></td>
          <td style="background-color:#ccccff;"><input name="6_5" value="0.00"/></td>
        </tr>
      </table>
    </form>
    <div style="margin-top: 15px" class="uk-text-center">
      <button @click="saveForm" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
      <button class="uk-button uk-button-primary" @click="dataRestore()"><i class="uk-icon-close"></i> 删除</button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-file-excel-o"></i> 导出Excel
      </button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-print"></i> 打印</button>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 44,  //当前表格的名称编号
        setting: [],
        com: []
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        this.com = this.$store.state.companyCurrent.gscompany
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        this.fn1('1_1', '2_1', 1)
        this.fn1('1_2', '2_2', 1)
        this.fn1('2_1', '2_2', 2)
        this.fn1('1_3', '2_3', 1)
        this.fn1('2_4', '2_3', 3)
        this.fn1('1_4', '2_4', 1)
        this.fn1('2_3', '2_4', 4)
        this.fn1('1_5', '2_5', 1)
        this.fn1('4_1', '6_1', 5)
        this.fn1('3_2', '6_2', 1)
        this.fn1('3_1', '3_2', 6)
        this.fn1('3_1', '3_2', 7)
        this.fn1('3_4', '6_4', 1)
        this.fn1('3_5', '3_4', 8)
        this.fn1('6_5', '6_4', 9)
      },
      // nameA 第一个单元格的name
      // nameB 第二个单元格的name
      // type  运算类型
      // {
      // 1:a = b
      // 2:a = b+c
      // 3:a如果小于等于b则  等于b  否则等于0
      // 4:a = b-c
      // }
      fn1(nameA, nameB, type) {
        let sum = 0
        let n1 = J('input[name=' + nameA + ']').val() * 1
        let n2 = J('input[name=' + nameB + ']').val() * 1

        if (type == 1) {
          sum = parseFloat(n2).toFixed(2)
          J('input[name=' + nameA + ']').val(sum)
        } else if (type == 2) {
          sum = n1 + n2
          J('input[name=2_3]').val(parseFloat(sum).toFixed(2))
        } else if (type == 3) {
          if (n1 <= n2) {
            sum = parseFloat(n1).toFixed(2)
          } else {
            sum = '0.00'
            this.$Message.error('本列需小于等于第三列数据')
          }
          J('input[name=2_4]').val(sum)
        } else if (type == 4) {
          sum = n1 - n2
          J('input[name=2_5]').val(parseFloat(sum).toFixed(2))
        } else if (type == 5) {
          sum = n1 + n2
          J('input[name=3_1]').val(parseFloat(sum).toFixed(2))
        } else if (type == 6) {
          sum = n1 - n2
          J('input[name=3_3]').val(parseFloat(sum).toFixed(2))
        } else if (type == 7) {
          sum = n1 - n2
          J('input[name=6_3]').val(parseFloat(sum).toFixed(2))
        } else if (type == 8) {
          let n3 = J('input[name=3_3]').val() * 1
          sum = n3 - n2
          J('input[name=' + nameA + ']').val(parseFloat(sum).toFixed(2))
        } else if (type == 9) {
          let n3 = J('input[name=6_3]').val() * 1
          sum = n3 - n2
          J('input[name=' + nameA + ']').val(parseFloat(sum).toFixed(2))
        }
      }
    }
  }
</script>

<style scoped>
  .p2 {
    text-align: center;
    width: 1200px;
    margin: 0px auto;
  }

  .biaoge input {
    width: 100%;
    height: 100%;
    border: 0px;
    background-color: initial;
  }
</style>



